import $ from 'jquery'
export const IWebSocket = (json) => {
    let options = {
        uri: "#", // SocketURI
        sockJsUri: "#",
        projectName: window.location.pathname.split("/")[1], // /URI2
        host: window.location.host, // IPIPPort
        onOpen: function (event) {
            // WSC
            console.log(event)
        },
        onMessage: function (event) {
            // WSC
            console.log(event)
        },
        onError: function (event) {
            // WSCWSC
            console.log(event)
        },
        onClose: function (event) {
            // WSCWSC
            console.log(event)
        }
    };
    $.extend(true, options, json);

    let websocket;
    if ('WebSocket' in window) {
        websocket = new WebSocket("wss://" + options.uri);
    } else if ('MozWebSocket' in window) {
        websocket = new MozWebSocket("wss://" + options.uri);
    } else {
        websocket = new SockJS("http://" + options.sockJsUri);
    }

    websocket.onopen = function (evnt) {

        options.onOpen(evnt);
    };
    websocket.onmessage = function (evnt) {

        options.onMessage(evnt);
    };
    websocket.onerror = function (evnt) {

        options.onError(evnt);
    };
    websocket.onclose = function (evnt) {

        options.onClose(evnt);
    };

    websocket.onSend = function (evnt) {
        console.log(evnt)
        websocket.send(evnt);
    }

    return websocket;
}


